@import url(https://fonts.googleapis.com/css2?family=Bokor&family=Open+Sans&display=swap);
#home {
  background-image: url(/bridge-in-tech-web/static/media/bg.08bf0a96.png);
  background-size: 100%;
  background-repeat: no-repeat;
  height: 80vh;
}
.header-text {
  max-width: 350px;
  margin-top: 30px;
  padding-top: 50px;
  margin-left: 16%;
}

.header-text h1 {
  color: #007bff;
  text-align: left;
  font-size: 35px;
  width: 500px;
  padding: 0;
  margin: 0 !important;

  font-family: "Tahoma";
}
#home p {
  margin-top: 20px;
  color: rgb(87, 85, 85);
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 1px;
}
@media only screen and (max-width: 700px) {
  .header-text h1 {
    font-size: 20px !important;
    width: 300px !important;
  }
  #home {
    background-image: none;
  }
}

@font-face {
    font-family: MetaProBold;
    src: url(/bridge-in-tech-web/static/media/MetaPro-Bold.fc44715d.otf) format("opentype");
  }

  @font-face {
    font-family: MetaProRegular;
    src: url(/bridge-in-tech-web/static/media/MetaPro-Normal.29b26753.otf) format("opentype");
  }

.GITcontainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    box-sizing: border-box;
    width: 100%;
    background-color: #ffffff;
    
}

.formContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    width:46%;
    padding: 4rem 0rem 3rem 4rem;
}

form{
    width:100%;
}
.message{
    height: 7rem;
}

label{
    font-family: MetaProBold;
    font-size: 1.2rem;
    line-height: 1.6rem;
    color: #1F2430 !important; 
}

.button{
    background: #4892C7;
    box-shadow: 0px 4px 10px rgba(72, 146, 199, 0.1);
    border: none;
    color: #FFFFFF;
    font-size: 1.5rem;
}

input{
    height:3.5rem;
    width:90%;
    margin-bottom: 2rem;
    border: 1px solid rgba(31, 36, 48, 0.6);
    border-radius: 5px;
    padding:0 2rem;
}

::-webkit-input-placeholder{
    font-family: MetaProRegular;
    font-size: 1rem;
    line-height: 1.5rem;
    color: rgba(31, 36, 48, 0.6);
}

:-ms-input-placeholder{
    font-family: MetaProRegular;
    font-size: 1rem;
    line-height: 1.5rem;
    color: rgba(31, 36, 48, 0.6);
}

::-ms-input-placeholder{
    font-family: MetaProRegular;
    font-size: 1rem;
    line-height: 1.5rem;
    color: rgba(31, 36, 48, 0.6);
}

::placeholder{
    font-family: MetaProRegular;
    font-size: 1rem;
    line-height: 1.5rem;
    color: rgba(31, 36, 48, 0.6);
}

.textContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    width:50%;
}

.heading{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    box-sizing: border-box;
    font-family: MetaProBold;
    font-size: 4rem;
    line-height: 97.8%;
    color: #1F2430;
    width: 60%;
  
}

.subheading{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    font-family: MetaProRegular;
    font-size: 1.5rem;
    line-height: 2rem; 
    width:60%;
}

@media screen and (max-width: 583px){

.GITcontainer{
    flex-direction: column-reverse;
}

.textContainer{
    width:100%;
    margin-bottom: 3rem;
}

.formContainer{
    width:100%;
    padding: 2rem 3rem;
}

input{
    width:100%;
}

.heading{
    align-items: center;
    width: 80%;
  
}

.subheading{
    text-align: center;
    line-height: 1.8rem; 
    width:90%;
}

}

@media screen and (max-width: 365px){
    .textContainer{
        margin-bottom: 1.5rem;
    }
    .heading{
        align-items: center;
        font-size: 3rem;
    }
}



.container-members {
  background: #f7f9fa;
  height: auto;
  width: 90vw;
  margin: 5em auto;
  box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.4);
}

label {
  color: #24b9b6;
  font-size: 1.2em;
  font-weight: 400;
}

h1 {
  color: #24b9b6;
  padding-top: 0.5em;
}

.error {
  color: red;
}
.error-message {
  color: #ff6565;
  padding: 0.5em 0.2em;
  height: 1em;
  position: absolute;
  font-size: 0.8em;
}

.field {
  border: 1px solid #1c6ea4;
  width: 100%;
  height: 2em;
}

.button-group {
  width: 100%;
}

.membersList {
  width: 85vw;
  text-align: center;
  padding: 2em;
  margin: 3em 3em;
  padding: 2em;
}

@media only screen and (max-width: 644px) {
  .container-members {
    overflow: auto;
    overflow-x: scroll;
  }
}

